:root {
  --safe-area-top: env(safe-area-inset-top);
  --safe-area-right: env(safe-area-inset-right);
  --safe-area-bottom: env(safe-area-inset-bottom);
  --safe-area-left: env(safe-area-inset-left);
}

* {
  padding: 0;
  margin: 0;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

html,
body {
  position: fixed;
  width: 100%;
  height: 100%;
}

.wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

pre {
  flex-shrink: 0;
  display: block;
  position: relative;
  margin: 0 auto;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 12px;
}

.field {
  background: linear-gradient(45deg, #dbf1ff 0%,#dbf1ff 40%,#c4e5fc 55%,#e3f2fd 100%);
  padding: 12px;
  border-radius: 8px;
  border: 4px solid #c2e5fd;
  color: #fff;
}

@media (min-width: 750px) {
  pre {
    margin: 20px 0;
    font-size: 14px;
  }
}

@media (min-width: 900px) {
  pre {
    font-size: 16px;
  }
}

@media (min-width: 1500px) {
  pre {
    font-size: 20px;
  }
}


@media (min-width: 2500px) {
  pre {
    font-size: 24px;
  }
}

.single-char {
  opacity: 0;
  position: absolute;
  left: -1000vw;
  color: transparent;
  pointer-events: none;
}

